	var validator = require('wxvalidator');
	export default {
		data() {
			return {
				// 基本信息
				basic: {
                    title: '', // 
                    phone: '', //
                    city: '', //
                    teachTime: '', // 授课时间
                    canNegotiate: true // 是否可以协商
				},
                student: {
                    gender: '', // 学生性别
                    subject: '', // 科目
                    detailAddress: '', // 大致地址，最多精确到小区
                    intro: '', // 学生情况描述
                },
                tutor: {
                    gender: '', // 家教性别
                    salary: 0, // 薪水，元/小时
                    duration: 0 ,
                    teachVia: '', // 家教上门，在线视频
                    request: '' // 对家教的要求
                },
				region: ['重庆市', '重庆市', '渝中区'],
				genderIndex: -1,
				gender: ['男', '女'],
				numList: [{
					name: '基本信息'
				}, {
					name: '学生情况'
				}, {
                    name: '老师要求'
                }],
				num: 0
			}
		},
		methods: {
			NextSteps(e) {
				if (this.num < this.numList.length - 1) {
					this.num = this.num + 1;
				}
			},
			PreviousSteps(e) {
				if (this.num > 0) {
					this.num = this.num - 1;
				}
			},
            getPhoneNumber (e) {
                console.log(e);
                if (e.type=="getphonenumber"){
                    
                }
            },
			degreeChange(e) {
				this.degreeIndex = e.detail.value;
			},
			RegionChange(e) {
			    this.region = e.detail.value;
			},
            setStudentIntro (e) {
                this.student.intro = e.detail.value;
                this.$forceUpdate();
            },
            ChooseIdCardImage(e) {
                uni.chooseImage({
                    count: 2, //默认9
                    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
                    sourceType: ['album'], //从相册选择
                    success: (res) => {
                        if (this.idCardImageList.length != 0) {
                                this.idCardImageList = this.idCardImageList.concat(res.tempFilePaths)
                        } else {
                                this.idCardImageList = res.tempFilePaths
                        }
                    }
                });
            },
            ViewIdCardImage(e) {
                uni.previewImage({
                    urls: this.idCardImageList,
                    current: e.currentTarget.dataset.url
                });
            },
            DelIdCardImg(e) {
                uni.showModal({
                    title: '你好',
                    content: '确定要删除图片吗？',
                    cancelText: '取消',
                    confirmText: '确定',
                    success: res => {
                        if (res.confirm) {
                                this.idCardImageList.splice(e.currentTarget.dataset.index, 1)
                        }
                    }
                })
            },
            ChooseCertificateImage(e) {
                uni.chooseImage({
                    count: 1, //默认9
                    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
                    sourceType: ['album'], //从相册选择
                    success: (res) => {
                        if (this.certificateImageList.length != 0) {
                                this.certificateImageList = this.certificateImageList.concat(res.tempFilePaths)
                        } else {
                                this.certificateImageList = res.tempFilePaths
                        }
                    }
                });
            },
            ViewCertificateImage(e) {
                uni.previewImage({
                    urls: this.certificateImageList,
                    current: e.currentTarget.dataset.url
                });
            },
            DelCertificateImg(e) {
                uni.showModal({
                    title: '你好',
                    content: '确定要删除图片吗？',
                    cancelText: '取消',
                    confirmText: '确定',
                    success: res => {
                        if (res.confirm) {
                            this.certificateImageList.splice(e.currentTarget.dataset.index, 1)
                        }
                    }
                })
            },
